@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');




$font-stack: Roboto, sans-serif;
$primary-color: #333;
$align : center;


@mixin centered-container{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}

html{
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
  font: 100% $font-stack;
  margin: 0;
  color: $primary-color;
  font-size: 1rem;
  display:flex;
  align-items: center;
  justify-content: center;
  
  height: 100%;
  background-color: #511730;
}

#quote-box {
    @include centered-container();
    justify-content: $align;
    align-items: center;
    text-align: $align;
    
    padding: 3rem;
    border: 5px solid #CB9173;
    height: 62.5vh;
    width: 62.5vw;

}

#text {
    @include centered-container();
    padding-bottom: 1.3rem;
    color: white;
    font-size: 1.7rem;
    width: 50vw;
    padding-bottom: 14.7vh;
}


#quotes{
    width:100px;
    height: 200px;
}

#author {

    color: white;
}

#new-quote{
   margin-top: 1rem;
}

#tweet-quote {
    margin-top: 1rem;
}

